<template>
  <v-container fluid>
    <v-list two-line>
      <template v-for="(itemValue, itemKey, itemIndex) in sitterList">
        <v-list-tile :key="itemKey">
          <v-list-tile-content>
            <v-list-tile-title>{{ itemValue.name }}</v-list-tile-title>
            <v-list-tile-sub-title>{{ itemValue.age }}</v-list-tile-sub-title>
          </v-list-tile-content>
          <v-list-tile-action>
            <v-btn icon ripple :to="'/sitters/' + itemKey">
              <v-icon color="grey lighten-1">edit</v-icon>
            </v-btn>
          </v-list-tile-action>
        </v-list-tile>
        <v-divider></v-divider>
      </template>
      <v-btn fixed bottom right fab dark color="indigo" :to="'/sitters/' + '-1'">
        <v-icon dark>add</v-icon>
      </v-btn>
    </v-list>
    <!--
    <div v-for="(itemValue, itemKey, itemIndex) in sitterList" :key="itemValue['.key']">

      <v-text-field label="Имя" v-model="itemValue.name"></v-text-field>
      <br>
      <label>age: </label>
      <input type="text" v-model="itemValue.age">
      <br>
      <label>itemKey = {{itemKey}}</label>
      <br>
      <v-btn small color="info" @click="deleteItem(itemKey)">delete</v-btn>
      <v-btn small color="info" @click="saveItem(itemValue, itemKey)">Save</v-btn>
      <hr>
    </div>
-->
  </v-container>
</template>

<script>
  var ref = firebase.database().ref("sitters");
  export default {
    data() {
      return {
        sitter: {
          name: "",
          age: ""
        },
        sitterList: {},
        selectedSitter: "",
        testvalue: "1111111111111"
      };
    },
    methods: {
      addSitter(name, age) {
        ref.push({
          name: name,
          age: age
        });
        this.sitter.name = "";
        this.sitter.age = "";
      },
      showItem(itemKey) {
        console.log(itemKey);
        //this.$router.push('/sitters/' + itemKey);@click="showItem(itemKey)"
      },
      deleteItem(itemKey) {
        console.log(itemKey);
        ref.child(itemKey).remove();
      },
      saveItem(itemValue, itemKey) {
        ref.child(itemKey).set({
          name: itemValue.name,
          age: itemValue.age
        });
        console.log(this.sitterList[itemKey]);
        console.log(this.sitterList[itemKey].name);
      }
    },
    created() {
      ref.on("value", val1 => {
        this.sitterList = val1.val();
        console.log(this.sitterList);
      });
    },
    name: "sitterList"
  };

  function showSitters() {
    ref.on("value", function (val1) {
      console.log(val1.val());
      sitterList.sitterList = val1.val();
    });
  }
</script>


