import Vue from 'vue'
import Router from 'vue-router'
import Base from '@/components/Base'
import clientList from '@/components/client/clientList'
import sitterList from '@/components/sitter/sitterList'
import client from '@/components/client/client'
import sitter from '@/components/sitter/sitter'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Base',
      component: Base
    },
    {
      path: '/clients',
      name: 'clientList',
      component: clientList
    },
    {
      path: '/clients/:id',
      name: 'client',
      component: client,
      props: true
    },    
    {
      path: '/sitters',
      name: 'sitterList',
      component: sitterList
    },
    {
      path: '/sitters/:id',
      name: 'sitter',
      component: sitter,
      props: true
    }             
  ],
  mode: 'history'
})



