<template>
    <v-container>
        <v-layout row wrap>
            <v-flex xs12>
                <v-text-field label="Имя" v-model="sitter.name"></v-text-field>
                <v-text-field label="Детально" v-model="sitter.age" multi-line></v-text-field>
                <v-btn small color="info" @click="saveItem">Сохранить</v-btn>
                <v-btn small color="info" @click="cancelItem">Отмена</v-btn>

            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
    export default {
        props: ["id"],
        data() {
            return {
                sitter: {
                    name: "",
                    age: ""
                }
            };
        },

        computed: {
            getId() {
                return this.id;
            }
        },
        created() {
            if (this.getId != -1) {
                firebase
                    .database()
                    .ref("sitters/" + this.getId)
                    .on("value", val1 => {
                        this.sitter = val1.val();
                        console.log(this.sitter);
                    });
            }
        },
        methods: {
            saveItem() {
                if (this.getId != -1) {
                    if (this.sitter.name == '') {
                        firebase
                            .database()
                            .ref("sitters/" + this.getId)
                            .remove();
                    } else {
                        firebase
                            .database()
                            .ref("sitters/" + this.getId)
                            .set({
                                name: this.sitter.name,
                                age: this.sitter.age
                            });
                    }
                } else {
                    firebase
                        .database()
                        .ref("sitters")
                        .push({
                            name: this.sitter.name,
                            age: this.sitter.age
                        });
                }
                this.$router.go(-1);
            },
            cancelItem() {
                this.$router.go(-1);
            },
        }
    };
</script>


