<template>
  <v-app>
    <v-navigation-drawer v-model="drawer" temporary app>
      <v-list>
        <v-list-tile v-for="item in menuItems" :key="item.title"
        router :to="item.link" >
          <v-list-tile-action><v-icon>{{item.icon}}</v-icon></v-list-tile-action>
          <v-list-tile-content><v-list-tile-title>{{item.title}}</v-list-tile-title></v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar color="indigo" dark fixed app>
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title>
        <router-link to="/" tag="span" style="cursor: pointer;">База</router-link>
        

      </v-toolbar-title>
    </v-toolbar>
    <v-content>
      <v-container fluid>
        <router-view></router-view>
      </v-container>
    </v-content>
    <v-footer color="indigo" app>
      <span class="white--text">&copy; 2018</span>
    </v-footer>
  </v-app>
</template>

<script>
export default {
  data() {
    return {
      fff: 'fgfgfg',
      drawer: null,
        menuItems: [
          { icon: "home", title: "База", link: "/" },
          { icon: "assignment_ind", title: "Сиделки", link: "/sitters" },           
          { icon: "supervisor_account", title: "Клиенты", link: "/clients" }
        ]
      
    };
  },
  name: "App"
};
</script>



